@media screen {
  body {
    display: none !important;
  }
}
@media print {
  * {
    font-family: "Arial", serif;
    font-weight: normal;
    box-sizing: border-box;
  }
  @page {
    size: A4;
    margin: 12pt 12pt 24pt 24pt;
  }
  /* @page :first {
     size: A4;
     margin: 12pt 12pt 24pt 24pt;
   }*/
  body {
    margin: 0;
    padding: 0;
    font-size: 11pt;
    counter-reset: page;
    /* div#header {
       position: fixed;
       top: 0;
       left: 0;
       width: 100%;
       counter-increment: page;
     }

     div#content {
       padding-top: 36pt;

       .content-section {
         padding-top: 36pt;
       }

       .content-section:first-child {
         padding-top: 0;
       }
     }*/
  }
  body #page-number:after {
    content: counter(page);
  }
  body .page-break {
    page-break-before: always;
  }
  table {
    page-break-inside: auto;
    /* This is the key */
  }
  table tr {
    page-break-inside: avoid;
    page-break-after: auto;
  }
  table thead {
    display: table-header-group;
  }
  table tfoot {
    display: table-footer-group;
  }
  table.table {
    margin: 0;
    width: 99.9%;
    border-collapse: collapse;
    font-size: 11pt;
    border-left: 0.1em solid #252525;
    border-right: 0.1em solid #252525;
    border-top: 0.1em solid #252525;
    border-bottom: 0.1em solid #252525;
  }
  table.table th {
    text-align: center;
    font-weight: bold;
  }
  table.table td {
    letter-spacing: 0.05em;
    vertical-align: top;
  }
  table.table td.border-bottom {
    border: 1px !important;
  }
  table.table td.border-0 {
    border: 0 !important;
  }
  table.table td.border-start-0 {
    border-left: 0 !important;
  }
  table.table td.border-end-0 {
    border-right: 0 !important;
  }
  table.table td.border-top-0 {
    border-top: 0 !important;
  }
  table.table td.border-bottom-0 {
    border-bottom: 0 !important;
  }
  table.table th, table.table td {
    padding: 1pt 2pt;
  }
  table.table.table-bordered th, table.table.table-bordered td {
    border-left: 0.1em solid #252525;
    border-right: 0;
    border-top: 0.1em solid #252525;
    border-bottom: 0;
    border-collapse: collapse;
  }
  table.table.table-detail-view {
    width: 50%;
  }
  table.table.table-borderless {
    border: none;
  }
  table.table.table-borderless tr, table.table.table-borderless td, table.table.table-borderless th {
    border: none;
  }
  .card {
    margin-top: 6pt;
    padding: 6pt 0;
  }
  .card .table th, .card .table td {
    padding: 5pt 0;
    border: none;
  }
  .card .table.table-grid-view th, .card .table.table-grid-view td {
    padding: 5pt;
    border: 1pt solid #000;
  }
  .card .table.table-grid-view thead tr th {
    text-align: center;
  }
  .text-start {
    text-align: left !important;
  }
  .text-center {
    text-align: center !important;
  }
  .text-end {
    text-align: right !important;
  }
  .border-bottom {
    border: 1px !important;
  }
  .border-0 {
    border: 0 !important;
  }
  .border-start-0 {
    border-left: 0 !important;
  }
  .border-end-0 {
    border-right: 0 !important;
  }
  .border-top-0 {
    border-top: 0 !important;
  }
  .border-bottom-0 {
    border-bottom: 0 !important;
  }
  .border-1 {
    border: 0.1em solid black !important;
  }
  .p-0 {
    padding: 0;
  }
  .pb-2 {
    padding-bottom: 1rem !important;
  }
  .pb-3 {
    padding-bottom: 1.5rem !important;
  }
  .m-0 {
    margin: 0;
  }
  .m-3 {
    margin: 1.5rem !important;
  }
  .m-5 {
    margin: 2.5rem !important;
  }
  .mt-1 {
    margin-top: 1rem !important;
  }
  .mt-2 {
    margin-top: 1.5rem !important;
  }
  .mt-3 {
    margin-top: 1.5rem !important;
  }
  .mt-5 {
    margin-bottom: 2.5rem !important;
  }
  .mb-1 {
    margin-bottom: 1rem !important;
  }
  .mb-2 {
    margin-bottom: 1.5rem !important;
  }
  .mb-3 {
    margin-bottom: 1.5rem !important;
  }
  .mb-5 {
    margin-bottom: 2.5rem !important;
  }
  .ms-1 {
    margin-left: 0.5rem !important;
  }
  .me-1 {
    margin-right: 0.5rem !important;
  }
  .font-weight-bold {
    font-weight: bold;
  }
  .text-nowrap {
    white-space: nowrap;
  }
  span.badge {
    font-size: large;
    font-style: normal;
    font-weight: bold;
  }
  span.badge.bg-danger {
    color: #CF0A0A;
  }
  span.badge.bg-primary {
    color: #540375;
  }
  span.badge.bg-success {
    color: #FFBF00;
  }
  span.badge.bg-warning {
    color: #FF7000;
  }
  span.badge.bg-info {
    color: #10A19D;
  }
  small.badge.bg-danger {
    color: #CF0A0A;
  }
  small.badge.bg-primary {
    color: #540375;
  }
  small.badge.bg-success {
    color: #FFBF00;
  }
  small.badge.bg-warning {
    color: #FF7000;
  }
  small.badge.bg-info {
    color: #10A19D;
  }
}